.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  background-color: #222;
  color: white;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1.0, 1.0);
          transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1.0, 1.0);
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.App-titleWrapper {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding-left: 10px;
  padding-right: 10px;
  border: 5px solid;
  -webkit-border-image: -webkit-gradient(linear, right top, left top, from(#f7ff00), to(#db36a4)) 10;
  -webkit-border-image: -webkit-linear-gradient(right, #f7ff00, #db36a4) 10;
       -o-border-image: -o-linear-gradient(right, #f7ff00, #db36a4) 10;
          border-image: -webkit-gradient(linear, right top, left top, from(#f7ff00), to(#db36a4)) 10;
          border-image: linear-gradient(to left, #f7ff00, #db36a4) 10;
}

.App-title {
  margin: 10px;
  text-align: center;
  color: white;
  font-size: 2.0em;
  font-weight: bold;
}

.App-theMagic {
  height: 120px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: 20px;
  margin-bottom: 20px;
  text-align: center;
  font-size: 1.5em;
  font-weight: bold;
}

.App-rateWrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.App-rateThumbWrapper {
  width: 45px;
  height: 45px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.App-rateThumb {
  margin: 10px;
  font-size: 1.5em;
  cursor: pointer;
}

@-webkit-keyframes bounceThumb {
  from, 20%, 40%, 60%, 80%, to { -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }
  0% { -webkit-transform: scale3d(1.0, 1.0, 1.0); transform: scale3d(1.0, 1.0, 1.0); }
  20% { -webkit-transform: scale3d(1.1, 1.1, 1.1); transform: scale3d(1.1, 1.1, 1.1); }
  40% { -webkit-transform: scale3d(0.9, 0.9, 0.9); transform: scale3d(0.9, 0.9, 0.9); }
  60% { -webkit-transform: scale3d(1.03, 1.03, 1.03); transform: scale3d(1.03, 1.03, 1.03); }
  80% { -webkit-transform: scale3d(0.97, 0.97, 0.97); transform: scale3d(0.97, 0.97, 0.97); }
  to { -webkit-transform: scale3d(1.0, 1.0, 1.0); transform: scale3d(1.0, 1.0, 1.0); }
}

@keyframes bounceThumb {
  from, 20%, 40%, 60%, 80%, to { -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }
  0% { -webkit-transform: scale3d(1.0, 1.0, 1.0); transform: scale3d(1.0, 1.0, 1.0); }
  20% { -webkit-transform: scale3d(1.1, 1.1, 1.1); transform: scale3d(1.1, 1.1, 1.1); }
  40% { -webkit-transform: scale3d(0.9, 0.9, 0.9); transform: scale3d(0.9, 0.9, 0.9); }
  60% { -webkit-transform: scale3d(1.03, 1.03, 1.03); transform: scale3d(1.03, 1.03, 1.03); }
  80% { -webkit-transform: scale3d(0.97, 0.97, 0.97); transform: scale3d(0.97, 0.97, 0.97); }
  to { -webkit-transform: scale3d(1.0, 1.0, 1.0); transform: scale3d(1.0, 1.0, 1.0); }
}

.App-animateThumb {
  -webkit-animation-duration: 0.75s;
          animation-duration: 0.75s;
  -webkit-animation-name: bounceThumb;
          animation-name: bounceThumb;
}

.App-another {
  margin: 10px;
  padding: 5px;
  border-radius: 100px;
  border: 3px solid #db36a4;
  background-color: #222;
  color: white;
  cursor: pointer;
  font-size: 1.25em;
  font-weight: bold;
  -webkit-transition: background-color 0.125s ease-in;
  -o-transition: background-color 0.125s ease-in;
  transition: background-color 0.125s ease-in;
}

@media (hover:hover) {
  .App-another:hover {
    background-color: #db36a4;
    border: 3px solid #db36a4;
    -webkit-transition: background-color 0.125s ease-in;
    -o-transition: background-color 0.125s ease-in;
    transition: background-color 0.125s ease-in;
  }
}

